import React, { useEffect, useState } from "react";

interface ImageData {
    src: string;
    filename: string;
    index: number;
}

const RealisationsSection = () => {
    const [images, setImages] = useState<ImageData[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const importAllImages = (r: any): ImageData[] => {
            return r.keys().map((filename: string, index: number) => ({
                src: r(filename) as string,
                filename: filename.replace('./', ''),
                index,
            }));
        }

        // @ts-ignore
        try {
            // @ts-ignore
            const imageContext = require.context(
                '../img/',
                false,
                /\.(png|jpe?g|svg)$/
            );
            setImages(importAllImages(imageContext));
        } catch (err) {
            setError("Nie udało się załadować galerii.");
        } finally {
            setLoading(false);
        }

        console.log(images);
    }, []);

    return (
        <section className="section" id="realizacje" aria-labelledby="realizacje-heading">
            <div className="container">
                <h2 id="realizacje-heading">Galeria</h2>
                <ul className="gallery" role="list">
                    {images.map((image) => (
                        <li>
                            <a className="glink" href={image.src} key={image.index}>
                                <img key={image.index} src={image.src} alt={`Realizacja ${image.index + 1}`} loading="lazy" />
                            </a>
                        </li>
                    ))}
                {/* <li><a className="glink" href="/img/thumbs/realizacja-1.jpg"><img src="/img/thumbs/realizacja-1.jpg" alt="Realizacja 1" loading="lazy" /></a></li>
                <li><a className="glink" href="/img/thumbs/realizacja-2.jpg"><img src="/img/thumbs/realizacja-2.jpg" alt="Realizacja 2" loading="lazy" /></a></li>
                <li><a className="glink" href="/img/thumbs/realizacja-3.jpg"><img src="/img/thumbs/realizacja-3.jpg" alt="Realizacja 3" loading="lazy" /></a></li>
                <li><a className="glink" href="/img/thumbs/realizacja-4.jpg"><img src="/img/thumbs/realizacja-4.jpg" alt="Realizacja 4" loading="lazy" /></a></li>
                <li><a className="glink" href="/img/thumbs/realizacja-5.jpg"><img src="/img/thumbs/realizacja-5.jpg" alt="Realizacja 5" loading="lazy" /></a></li>
                <li><a className="glink" href="/img/thumbs/realizacja-6.jpg"><img src="/img/thumbs/realizacja-6.jpg" alt="Realizacja 6" loading="lazy" /></a></li> */}
                </ul>
            </div>
            <div className="lightbox" hidden>
                <button className="lb-close" aria-label="Zamknij">×</button>
                <img className="lb-img" alt="Podgląd realizacji" />
            </div>
        </section>
    );
}

export default RealisationsSection;