import React, { useState } from "react";
import ContactSection from "./Contact";

interface ErrorField {
    name?: string;
    email?: string;
    message?: string;
}

const ContactForm = () => {
    const [fields, setFields] = useState({ name: "", email: "", message: "" });
    const [errors, setErrors] = useState<ErrorField>({});
    const [status, setStatus] = useState("");

    const validate = () => {
        const err: ErrorField = {};

        if(!fields.name.trim()) err.name = "Imię i nazwisko jest wymagane.";
        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(fields.email)) err.email = "Podaj poprawny adres e-mail.";
        if (fields.message.trim().length < 10) err.message = "Wiadomość min. 10 znaków.";

        return err;
    }

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setFields({ ...fields, [e.target.name]: e.target.value });
        setErrors({ ...errors, [e.target.name]: "" });
    }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        const err = validate();

        if(Object.keys(err).length) {
            setErrors(err);
            setStatus("Uzupełnij poprawnie wszystkie pola.");
            return;
        }

        setStatus("Wysyłanie...");

        try {
            const res = await fetch("http://localhost:5000/api/contact", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(fields),
            });

            const txt = await res.text();
            setStatus(txt);
            setFields({ name: "", email: "", message: "" });
        } catch {
            setStatus("Coś poszło nie tak. Spróbuj ponownie później.");
        }
    }

    return (
        <section className="section" id="kontakt" aria-labelledby="kontakt-heading">
            <div className="container">
                <h2 id="kontakt-heading">Skontaktuj się z nami</h2>
                <div className="grid cols-2">
                    <form className="form" onSubmit={handleSubmit} noValidate>
                        <div className="form-field">
                            <label htmlFor="name">Imię i nazwisko</label>
                            <input disabled id="name" name="name" type="text" value={fields.name} onChange={handleChange} autoComplete="name" required />
                            <small className="error" aria-live="polite">{errors.name}</small>
                        </div>
                        <div className="form-field">
                            <label htmlFor="email">E-mail</label>
                            <input disabled id="email" name="email" type="email" value={fields.email} onChange={handleChange} autoComplete="email" required />
                            <small className="error" aria-live="polite">{errors.email}</small>
                        </div>
                        <div className="form-field">
                            <label htmlFor="message">Wiadomość</label>
                            <textarea disabled id="message" name="message" rows={6} value={fields.message} onChange={handleChange} required></textarea>
                            <small className="error" aria-live="polite">{errors.message}</small>
                        </div>
                        <button disabled className="btn" type="submit">Wyślij</button>
                        {/* <p className="form-msg" role="status" aria-live="polite">{status}</p> */}
                        <p className="form-msg" style={{ color: 'red' }} role="status" aria-live="polite">Formularz tymczasowo wyłączony, zapraszamy do innych sposobów kontaktu.</p>
                    </form>

                    <ContactSection />
                </div>
            </div>
        </section>
    );
}

export default ContactForm;